import React, { Component } from "react";
import { constantVariables } from "./Validation/FormData";

class LeadCheckApp extends Component {    
    constructor(props) {
        super(props);
        this.state = {
            isError: false,
            AgreeTerms: true,
            AgreePqTerms: true,
            AgreeConsent: true,
            AgreeEmail: true,
            AgreeTCPA : false,
            showModal: false,
        }
    } 
    componentDidMount() {            
        this.setState({ showModal : false});
    }       
    AgreeTCPAChange(e) {
        this.setState({ AgreeTCPA: e.target.checked });
    }
    AgreePqTermsChange(e) {
        //this.setState({ AgreePqTerms: e.target.checked });
        this.setState({ AgreePqTerms: document.getElementById('agreePqTerms').checked, isError: false });
    }
    
    Submit(e){
        e.preventDefault();
        var that = this;      
        that.setState({ showModal : true});
        var second = 0;
        if(window.presubmit !== undefined) window.presubmit();
        var x = setInterval(function() {
            //that.setState({loaderCheck : second });
            if(second < 1){
                if(!that.state.IsFormSubmitted) {  
                    document.getElementById('frmPrequalify').submit();
                    //document.getElementById('frmPrequalify').submit();
                    that.setState({ IsFormSubmitted: true });                                
                    window.scrollTo(0, 0);
                }    
            } 
            second++; 
            
            if( second > 5 && second <= 10)
            { 
                //that.state.loaderContent = 'We\'re still checking...';
                that.setState({ loaderContent : 'We\'re still checking...'}); 
            }
            if( second > 10 && second <= 15)
            { 
                //that.state.loaderContent = 'Thank you for your patience...';
                that.setState({ loaderContent : 'Thank you for your patience...'});
            }
            if( second > 15) 
            { 
                //that.state.loaderContent ='Just a few moments longer....';
                that.setState({ loaderContent : 'Just a few moments longer....'}); 
                clearInterval(x);
            }
        }, 1000);
          
    }
    render() {
        const uuid = typeof window.uuid === typeof undefined ? '' :  window.uuid;
        return (
            <div className="container" >
                {/* <!--Header section --> */}  
                {/* {!this.state.showModal ?                */}
                <section id="headersection" className="">
                    <div className="row">
                        <div className="col-md-1">
                        </div>
                        <div className="center-block col-md-12">
                            {/* <div className={ ` ${!this.state.showModal ? 'box-panel clearfix access-offer-top-margin' : ''}`} id="mainbox"> */}
                            <div className={ `box-panel clearfix access-offer-top-margin`} id="mainbox">
                                <div className="container-box-content" id="mainboxcontent">
                                <form action={`${constantVariables.prequalifyPostURL}`}  id="frmPrequalify" disable-btn="" className="app-form" method="POST" onSubmit={this.Submit.bind(this)}>
                                <input type="hidden" name="TransactionType" value="leadCheckApp" />
                                <input type="hidden" name="deviceType" value={`${this.props.currentDevice ? this.props.currentDevice : ''}`} />
                                <input type="hidden" name="OS" value={`${this.props.currentOS ? this.props.currentOS : ''}`} />
                                <input type="hidden" name="browser" value={`${this.props.currentBrowser ? this.props.currentBrowser : ''}`} />
                                <input type="hidden" name="leadCheckID" value={`${this.props.leadCheckID ? this.props.leadCheckID : ''}`} />
                                <input type="hidden" name="uuid" value={uuid} />
                                <input type="hidden" name="MatchKey" value={uuid} />
                                    <div className="row">
                                        <center>
                                    {  this.props.currentDevice !== 'Mobile' && <span>
                                        <h2 className="firstSection intercom-font-weight title-iphone-5"> Welcome to PrimeRates! </h2>
                                        <h3 className="firstSection intercom-font-weight title-iphone-5"> To See your offers from lender partners check the boxes and click the button below to continue. </h3>
                                    </span>
                                    }
                                    {  this.props.currentDevice === 'Mobile' && <span>
                                        <h2 className="firstSection intercom-font-weight "> Welcome to PrimeRates! </h2>
                                        <h4 className="firstSection intercom-font-weight "> To See your offers from lender partners check the boxes and click the button below to continue. </h4>
                                    </span>
                                    }
                                    </center>
                                    </div>
                                    <center>
                                    {!this.state.showModal &&
                                            <div className="row" >
                                                {this.props.currentDevice === 'Mobile' && <span>
                                                    <button type="submit" id="viewpreqoffers" name="viewpreqoffers" tabIndex="26" className="btn btn-primary leadcheck-button" disabled={this.state.initialDisabled} >View Pre-Qualified Offers</button>
                                                    <h5 className="formerror">No impact to your credit score</h5>
                                                    <div className={` ${this.state.isError ? ' all-error-message ' : ''}`}>
                                                        <p> {this.state.isError &&  <span className="formerror-agreement ">{this.state.IntercomErrorMessage}</span>}</p>
                                                    </div>
                                    </span> }
                                            </div>
                                            }

                                    </center>
                                        <div className="row">
                                            <div className="col-md-12">
                                            <div className="checkbox">
                                                <input name="agreePqTerms" id="agreePqTerms" tabIndex="23" checked={this.state.AgreePqTerms} value={this.state.AgreePqTerms} className={`checkbox pre-checkbox ${!this.state.AgreePqTerms ? 'has-error' : ''}`} type="checkbox" onChange={this.AgreePqTermsChange.bind(this)}/>
                                                    
                                                    <p className="formlist2">I authorize {this.props.IsHeadWay ? this.props.configSettings.HeadWayTheme : this.props.configSettings.PrimeRateTheme } and its <a className="loan-app info-tooltip tooltip-width">            
                                                    <span className="tooltiptext tooltiptext-partner tooltip-top-margin-partnerlink"><b>{this.props.IsHeadWay ? this.props.configSettings.HeadWayTheme : this.props.configSettings.PrimeRateTheme } Personal Loan Partners</b>: {this.props.IsHeadWay ? 'Prosper, Upgrade, Marcus' : ' Best Egg, OneMain, SoFi, Upgrade, Prosper, FreedomPlus, Payoff, LightStream, Marcus' }  </span>
                                                    partners </a> to obtain consumer reports and related information about me from one or more consumer reporting agencies, such as TransUnion, Experian, or Equifax. I also authorize PrimeRates and its partners to share information about me and to <a className="loan-app info-tooltip tooltip-width-send-email-message-me">            
                                                    <span className="tooltiptext tooltiptext-send-email-message-me tooltip-top-margin-send-email-message-me">I understand and agree that I am authorizing {this.props.IsHeadWay ? this.props.configSettings.HeadWayTheme : this.props.configSettings.PrimeRateTheme } and its partners to send me special offers and promotions via email.</span>
                                                    send me email messages </a>. Additionally, I agree to the {this.props.IsHeadWay ? this.props.configSettings.HeadWayTheme : this.props.configSettings.PrimeRateTheme } <a href={`${this.props.IsHeadWay ? 'https://www.headwaysales.com/terms-of-use' : '/terms-of-use'}`} target="_blank" rel="noopener noreferrer" className="underline-on-hover">Terms of Use</a>, <a href={`${this.props.IsHeadWay ? 'https://www.headwaysales.com/privacy-policy' : '/privacy-policy'}`} target="_blank" rel="noopener noreferrer" className="underline-on-hover">Privacy Policy</a>, and <a href={`${this.props.IsHeadWay ? 'https://www.headwaysales.com/e-sign' : '/e-sign'}`} target="_blank" className="underline-on-hover">Consent to Electronic Communications</a>. {!this.state.AgreePqTerms && <span className="formerror-agreement  " >Please agree to terms and conditions</span> }</p>
                                                     
                                                </div>
                                                <div className="checkbox">
                                                
                                                        <input id='agreeTCPA' type='hidden' value={this.state.AgreeTCPA} name="agreeTCPA" />
                                                        <input className="checkbox pre-checkbox" id='chk-agreeTCPA' type="checkbox" value={this.state.AgreeTCPA} tabIndex="24" onChange={this.AgreeTCPAChange.bind(this)}/>
                                                        
                                                    <p className="formlist2">I agree to allow {this.props.IsHeadWay ? this.props.configSettings.HeadWayTheme : this.props.configSettings.PrimeRateTheme} and its <a className="loan-app info-tooltip tooltip-width">            
                                                    <span className="tooltiptext tooltiptext-partner tooltip-top-margin-partnerlink"><b>{this.props.IsHeadWay ? this.props.configSettings.HeadWayTheme : this.props.configSettings.PrimeRateTheme } Personal Loan Partners</b>: {this.props.IsHeadWay ? 'Prosper, Upgrade, Marcus' : ' Best Egg, OneMain, SoFi, Upgrade, Prosper, FreedomPlus, Payoff, LightStream, Marcus' }  </span>
                                                    partners </a> to <a className="loan-app info-tooltip tooltip-width-call-text-me">            
                                                    <span className={`tooltiptext tooltiptext-call-text-me ${this.props.IsHeadWay ? 'tooltip-top-margin-call-text-meHW' : 'tooltip-top-margin-call-text-me'} `}>I authorize {this.props.IsHeadWay ? this.props.configSettings.HeadWayTheme : this.props.configSettings.PrimeRateTheme } and its partner lenders, service providers, affiliates, and successors and assigns, to deliver telemarketing calls and text messages to each telephone number that I provide in connection with my inquiry and application for credit using an automatic telephone dialing system and/or a prerecorded or artificial voice message. I understand I am not required to provide this authorization as a condition of purchasing any property, goods or services. I certify that I have authority to provide this consent either because I am the subscriber of these telephone numbers or I am a non-subscriber customary user who has authority to consent to these communications. If I would like to revoke this consent, I agree to send an email to {this.props.IsHeadWay ? `customerservice@headwaysales.com` : 'customerservice@primerates.com' } with "Phone Call Opt-Out" in the subject line.  </span>
                                                    call or text me </a> at the phone numbers I have provided to discuss my application for credit and for other marketing purposes. (optional)</p>
                                                    
                                                </div> 
                                            </div>
                                        </div> 
                                        <center>
                                        {!this.state.showModal &&
                                            <div className="row" id="formbutton">
                                                  {this.props.currentDevice !== 'Mobile' ?  <span>
                                                <h4 className="formerror"> Your submission will not impact your credit score </h4>
                                                <div className="all-error-message">
                                                    <p> {this.state.isError &&  <span className="formerror-agreement ">{this.state.IntercomErrorMessage}</span>}</p>
                                                </div>
                                                <button type="submit" id="viewpreqoffers" name="viewpreqoffers" tabIndex="26" className="btn btn-primary intercome-button" disabled={this.state.initialDisabled} >View Pre-Qualified Offers</button>
                                                </span> : ''}
                                                    {(this.state.IsIntercom || this.state.IsAcquireInteractive) && this.props.currentDevice === 'Mobile' ? 
                                                    <div className="row disclosure">
                                                    <div className="col-md-12">                                                   
                                                    <div className="checkbox">
                                                        <input name="agreePqTerms" id="agreePqTerms" tabIndex="23" checked={this.state.AgreePqTerms} value={this.state.AgreePqTerms} className={`mobile-checkbox  checkbox pre-checkbox ${!this.state.AgreePqTerms ? 'has-error' : ''}`} type="checkbox" onChange={this.AgreePqTermsChange.bind(this)}/>

                                                            <p className="formlist2 intercom-consent-alignment">I authorize {this.props.IsHeadWay ? this.props.configSettings.HeadWayTheme : this.props.configSettings.PrimeRateTheme } and its <a className="loan-app info-tooltip tooltip-width">            
                                                            <span className="tooltiptext tooltiptext-partner tooltip-top-margin-partnerlink"><b>{this.props.IsHeadWay ? this.props.configSettings.HeadWayTheme : this.props.configSettings.PrimeRateTheme } Personal Loan Partners</b>: {this.props.IsHeadWay ? 'Prosper, Upgrade, Marcus' : ' Best Egg, OneMain, SoFi, Upgrade, Prosper, FreedomPlus, Payoff, LightStream, Marcus' }  </span>
                                                            partners </a> to obtain consumer reports and related information about me from one or more consumer reporting agencies, such as TransUnion, Experian, or Equifax. I also authorize PrimeRates and its partners to share information about me and to <a className="loan-app info-tooltip tooltip-width-send-email-message-me">            
                                                            <span className="tooltiptext tooltiptext-send-email-message-me tooltip-top-margin-send-email-message-me">I understand and agree that I am authorizing {this.props.IsHeadWay ? this.props.configSettings.HeadWayTheme : this.props.configSettings.PrimeRateTheme } and its partners to send me special offers and promotions via email.</span>
                                                            send me email messages </a>. Additionally, I agree to the {this.props.IsHeadWay ? this.props.configSettings.HeadWayTheme : this.props.configSettings.PrimeRateTheme } <a href={`${this.props.IsHeadWay ? 'https://www.headwaysales.com/terms-of-use' : '/terms-of-use'}`} target="_blank" rel="noopener noreferrer" className="underline-on-hover">Terms of Use</a>, <a href={`${this.props.IsHeadWay ? 'https://www.headwaysales.com/privacy-policy' : '/privacy-policy'}`} target="_blank" rel="noopener noreferrer" className="underline-on-hover">Privacy Policy</a>, and <a href={`${this.props.IsHeadWay ? 'https://www.headwaysales.com/e-sign' : '/e-sign'}`} target="_blank" className="underline-on-hover">Consent to Electronic Communications</a>.</p>
                                                            
                                                        </div>
                                                        <div className="checkbox">
                                                        
                                                                <input id='agreeTCPA' type='hidden' value={this.state.AgreeTCPA} name="agreeTCPA" />
                                                                <input className={` checkbox pre-checkbox mobile-checkbox `} id='chk-agreeTCPA' type="checkbox" value={this.state.AgreeTCPA} tabIndex="24" onChange={this.AgreeTCPAChange.bind(this)}/>
                                                             
                                                            <p className="formlist2 intercom-consent-alignment">I agree to allow {this.props.IsHeadWay ? this.props.configSettings.HeadWayTheme : this.props.configSettings.PrimeRateTheme} and its <a className="loan-app info-tooltip tooltip-width">            
                                                            <span className="tooltiptext tooltiptext-partner tooltip-top-margin-partnerlink"><b>{this.props.IsHeadWay ? this.props.configSettings.HeadWayTheme : this.props.configSettings.PrimeRateTheme } Personal Loan Partners</b>: {this.props.IsHeadWay ? 'Prosper, Upgrade, Marcus' : ' Best Egg, OneMain, SoFi, Upgrade, Prosper, FreedomPlus, Payoff, LightStream, Marcus' }  </span>
                                                            partners </a> to <a className="loan-app info-tooltip tooltip-width-call-text-me">            
                                                            <span className={`tooltiptext tooltiptext-call-text-me ${this.props.IsHeadWay ? 'tooltip-top-margin-call-text-meHW' : 'tooltip-top-margin-call-text-me'} `}>I authorize {this.props.IsHeadWay ? this.props.configSettings.HeadWayTheme : this.props.configSettings.PrimeRateTheme } and its partner lenders, service providers, affiliates, and successors and assigns, to deliver telemarketing calls and text messages to each telephone number that I provide in connection with my inquiry and application for credit using an automatic telephone dialing system and/or a prerecorded or artificial voice message. I understand I am not required to provide this authorization as a condition of purchasing any property, goods or services. I certify that I have authority to provide this consent either because I am the subscriber of these telephone numbers or I am a non-subscriber customary user who has authority to consent to these communications. If I would like to revoke this consent, I agree to send an email to {this.props.IsHeadWay ? `customerservice@headwaysales.com` : 'customerservice@primerates.com' } with "Phone Call Opt-Out" in the subject line.  </span>
                                                            call or text me </a> at the phone numbers I have provided to discuss my application for credit and for other marketing purposes. (optional)</p>
                                                             
                                                        </div> 
                                                    </div>
                                                </div>
                                                : ''}
                                                </div> }
                                        </center>
                                        { this.state.showModal &&
                                            <center>
                                                <div className="row" id="formbutton">
                                                    <div id="myModal" className={`modal displayblock`}>
                                                        <div className="modal-content">                                                                                                
                                                            <div className="trans-wrap loans">
                                                                <div className="trans-logo">
                                                                    <img src={this.props.configSettings.primeRateLogo} alt="" />
                                                                </div>
                                                                <div className="trans-statment">
                                                                    <img src={this.props.configSettings.lockImage} alt="" />
                                                                    <h1>Checking our partner network for loan offers…</h1>
                                                                </div>
                                                                <div className={`${this.props.currentBrowser === 'safari' ? 'load-img-mobile-safari' : 'load-img'}`}>  
                                                                { this.props.currentBrowser === 'safari' ?     <i className={`fa fa-circle-o-notch fa-spin ${this.props.currentBrowser === 'safari' ? ' safari ' : ''}`}></i>  : '' }                                                                                                                                 
                                                                { this.props.currentBrowser === 'safari' ? <img src={`https://${window.cdn  !== undefined ? window.cdn : window.location.hostname}${this.props.configSettings.safariloadImage}`} alt="PrimeRates" width="300" /> :
                                                                <img src={`https://${window.cdn  !== undefined ? window.cdn : window.location.hostname}${this.props.configSettings.spinnerGIF}`} alt={ this.props.configSettings.PrimeRateTheme } width="250" /> }
                                                            </div>
                                                                <div className="website-text">
                                                                <p> {this.state.loaderContent}</p>
                                                                </div>
                                                            </div> 
                                                        </div>
                                                    </div> 
                                                </div> </center>  }
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>                  
            </div>
        );
    }
}

export default LeadCheckApp;


